@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:700);
*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

body {
  margin: 0;
  line-height: 1.6;
  font-family: "Lato", sans-serif;
  color: #6A737D; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif;
  margin: 0; }

img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: block; }

ul {
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

p {
  margin: 0; }

.l-container {
  max-width: 1200px;
  margin: 0 auto; }

.input {
  font-family: "Lato", sans-serif;
  border: none;
  border-radius: 3px;
  padding: 8px;
  outline: none;
  width: 175px; }
  .input--secondary {
    background: #3F4448;
    color: #ffffff; }

.button {
  background-color: #F8F9FB;
  color: #24292E;
  border: none;
  padding: 6px 12px;
  font-family: "Lato", sans-serif;
  border-radius: 3px;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
  outline: none; }
  .button:hover {
    background-color: #e8ebf2; }
  .button--secondary {
    color: #ffffff;
    background-color: #30C352; }
    .button--secondary:hover {
      background-color: #2baf49; }

.header {
  width: 100%;
  background: #24292E;
  color: #ffffff;
  height: 55px; }
  .header-container {
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .header-container__main {
      display: flex;
      align-items: center;
      justify-content: space-between; }
    .header-container__logo {
      margin-right: 16px; }
    .header-container__link {
      color: #ffffff;
      margin: 0 16px; }


.spinner {
  margin: 50px auto;
  width: 50px;
  height: 50px;
  position: relative;
  text-align: center;
  -webkit-animation: sk-rotate 2.0s infinite linear;
  animation: sk-rotate 2.0s infinite linear; }

.dot1, .dot2 {
  width: 60%;
  height: 60%;
  display: inline-block;
  position: absolute;
  top: 0;
  background-color: #24292E;
  border-radius: 100%;
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
  animation: sk-bounce 2.0s infinite ease-in-out; }

.dot2 {
  top: auto;
  bottom: 0;
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s; }

@-webkit-keyframes sk-rotate {
  100% {
    -webkit-transform: rotate(360deg); } }

@keyframes sk-rotate {
  100% {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg); } }

@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0); }
  50% {
    -webkit-transform: scale(1); } }

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0); }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1); } }

